.wrapper {
  display: flex;
  flex-direction: column;
  max-width: 400px;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  padding: 5px 30px;
  background-color: #fff;
  border: 2px solid darkgray;
  border-radius: 10px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

label {
  display: block;
  margin-bottom: 10px;
  margin-top: 10px;
}

.button {
  background-color: dodgerblue;
  color: black;
  font-size: 16px;
  padding: 8px 20px;
  margin-top: 10px;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  text-align: center;
  text-transform: capitalize;
  max-width: 300px;
  margin-left: auto;
  margin-right: auto;
}

.button:hover {
  background-color: darkblue;
  color: white;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

.button:not(:last-child) {
  margin-right: 15px;
}
